___CSS_LOADER_EXPORT___.push([module.id, ".gallery-form-photo{background-size:cover;height:auto;margin:auto;width:100%}.close-icon-container{align-items:flex-start;display:flex;height:100%;justify-content:flex-end;left:0;position:absolute;top:0;width:100%}", "",{"version":3,"sources":["webpack://./resources/js/views/admin/forms/Gallery.vue"],"names":[],"mappings":"AA0RA,oBACA,qBAAA,CAEA,WAAA,CACA,WAAA,CAFA,UAGA,CAKA,sBAQA,sBAAA,CAFA,YAAA,CADA,WAAA,CAEA,wBAAA,CALA,MAAA,CADA,iBAAA,CAEA,KAAA,CACA,UAKA","sourcesContent":["<template>\r\n  <v-main>\r\n    <v-container>\r\n      <v-card>\r\n        <v-card-title class=\"justify-content-center\">\r\n          <h2\r\n            v-if=\"formTitle != 'undefined undefined'\"\r\n            class=\"pt-4 font-weight-bold panel-title-header first-color\"\r\n          >\r\n            {{\r\n              $route.path.split(\"/\")[2] == \"realizations\"\r\n                ? \"Realizacje\"\r\n                : \"Galeria - \" + formTitle\r\n            }}\r\n          </h2>\r\n        </v-card-title>\r\n        <v-divider class=\"mt-0\"></v-divider>\r\n        <v-form ref=\"form\">\r\n          <v-row>\r\n            <v-col cols=\"12\" md=\"8\">\r\n              <div class=\"pa-5\">\r\n                <div class=\"gallery-container\">\r\n                  <v-row>\r\n                    <v-col\r\n                      v-for=\"photo in gallery\"\r\n                      :key=\"photo.path\"\r\n                      cols=\"12\"\r\n                      sm=\"6\"\r\n                      md=\"4\"\r\n                    >\r\n                      <div class=\"position-relative\">\r\n                        <Picture\r\n                          classImg=\"img-fluid\"\r\n                          :src=\"getUrl(photo.path)\"\r\n                        />\r\n                        <div class=\"close-icon-container\">\r\n                          <v-icon\r\n                            v-if=\"photo.id\"\r\n                            @click=\"destroy(photo.id)\"\r\n                            class=\"pr-1 pt-1 close-icon\"\r\n                            color=\"grey\"\r\n                            >mdi-close</v-icon\r\n                          >\r\n                        </div>\r\n                      </div>\r\n                      <v-text-field\r\n                        label=\"Tekst alternatywny\"\r\n                        v-model=\"photo.photo_alt\"\r\n                      ></v-text-field>\r\n                    </v-col>\r\n                  </v-row>\r\n                </div>\r\n              </div>\r\n            </v-col>\r\n\r\n            <v-col cols=\"12\" md=\"4\">\r\n              <div class=\"pa-5 d-flex flex-column justify-content-between\">\r\n                <div>\r\n                  <GalleryPicker\r\n                    @updateDeletedPhoto=\"updateDeletedPhoto\"\r\n                    @loadedImage=\"setImagePlaceholder\"\r\n                    :apiGallery=\"apiGallery\"\r\n                  />\r\n                </div>\r\n              </div>\r\n            </v-col>\r\n          </v-row>\r\n          <v-divider class=\"mb-0\"></v-divider>\r\n          <v-card-actions class=\"pa-4\">\r\n            <v-btn\r\n              :disabled=\"gallery.length === 0\"\r\n              color=\"primary\"\r\n              class=\"mr-2\"\r\n              @click=\"send\"\r\n            >\r\n              <v-icon left>mdi-check</v-icon>\r\n              <span>Zatwierdź</span>\r\n            </v-btn>\r\n\r\n            <v-btn color=\"error\" class=\"mr-2\" @click=\"$router.go(-1)\">\r\n              <v-icon left>mdi-close</v-icon>\r\n              <span>Anuluj</span>\r\n            </v-btn>\r\n          </v-card-actions>\r\n        </v-form>\r\n      </v-card>\r\n    </v-container>\r\n  </v-main>\r\n</template>\r\n\r\n<script>\r\nimport axios from \"axios\";\r\nimport GalleryPicker from \"@/components/gallery-picker/GalleryPicker\";\r\nimport SnackbarAlerts from \"@/data/snackbar-alerts.js\";\r\nimport url from \"@/helpers/photo/url.js\";\r\nimport Picture from \"@/components/picture/Picture.vue\";\r\nimport {\r\n  VMain,\r\n  VContainer,\r\n  VCard,\r\n  VForm,\r\n  VBtn,\r\n  VIcon,\r\n  VCardActions,\r\n  VDivider,\r\n  VRow,\r\n  VCol,\r\n  VTextField,\r\n  VCardTitle,\r\n} from \"vuetify/lib\";\r\n\r\nexport default {\r\n  components: {\r\n    GalleryPicker,\r\n    Picture,\r\n    VMain,\r\n    VContainer,\r\n    VCard,\r\n    VForm,\r\n    VBtn,\r\n    VIcon,\r\n    VCardActions,\r\n    VDivider,\r\n    VRow,\r\n    VCol,\r\n    VTextField,\r\n    VCardTitle,\r\n  },\r\n  data: () => ({\r\n    name: \"\",\r\n    rules: {\r\n      titleRules: [(v) => !!v || \"Tytuł jest wymagany!\"],\r\n    },\r\n    apiGallery: [],\r\n    pickedGallery: [],\r\n    gallery: [],\r\n    currentObject: {},\r\n    activePhoto: \"https://via.placeholder.com/250\",\r\n    img: \"\",\r\n  }),\r\n  computed: {\r\n    formTitle() {\r\n      if (this.$route.path.split(\"/\")[2] == \"players\")\r\n        return `${this.currentObject.first_name} ${this.currentObject.last_name}`;\r\n      else return this.currentObject.title;\r\n    },\r\n  },\r\n  methods: {\r\n    getUrl(src) {\r\n      return url(src);\r\n    },\r\n    getImageDefaultPlaceholder() {\r\n      return \"https://via.placeholder.com/250\";\r\n    },\r\n    setGallery(event) {\r\n      let pickedGallery = [];\r\n      for (let photo of event) {\r\n        pickedGallery.push({\r\n          path: photo,\r\n          photo_alt: \"\",\r\n          item_id: this.$route.params.id,\r\n          table_name: this.$route.params.table,\r\n        });\r\n      }\r\n      this.pickedGallery = pickedGallery;\r\n      this.gallery = [...this.apiGallery, ...this.pickedGallery];\r\n    },\r\n    setImagePlaceholder(event) {\r\n      if (event === \"placeholder\") {\r\n        (this.img = \"\"), (this.activePhoto = this.getImageDefaultPlaceholder());\r\n      } else {\r\n        this.setGallery(event);\r\n        this.activePhoto = url(event);\r\n      }\r\n    },\r\n    getFormData() {\r\n      return this.gallery;\r\n    },\r\n    resetForm() {\r\n      this.activePhoto = this.getImageDefaultPlaceholder();\r\n    },\r\n    setLoading(state) {\r\n      this.$store.commit(\"loading\", state);\r\n    },\r\n    add(formData) {\r\n      axios\r\n        .post(`/api/gallery/add`, formData, {\r\n          headers: {\r\n            \"Content-Type\": \"application/json\",\r\n          },\r\n        })\r\n        .then((res) => {\r\n          this.setLoading(false);\r\n          this.$store.commit(\"setSnackbar\", SnackbarAlerts.success);\r\n          this.loadGallery();\r\n          this.resetForm();\r\n        })\r\n        .catch((err) => {\r\n          this.$store.commit(\"setSnackbar\", SnackbarAlerts.error);\r\n          this.setLoading(false);\r\n        });\r\n    },\r\n    edit(formData) {\r\n      axios\r\n        .put(`/api/gallery/edit`, formData, {\r\n          headers: {\r\n            \"Content-Type\": \"application/json\",\r\n          },\r\n        })\r\n        .then((res) => {\r\n          this.setLoading(false);\r\n          this.$store.commit(\"setSnackbar\", SnackbarAlerts.success);\r\n          this.loadGallery();\r\n        })\r\n        .catch((err) => {\r\n          this.$store.commit(\"setSnackbar\", SnackbarAlerts.error);\r\n          this.setLoading(false);\r\n          console.log(err);\r\n        });\r\n    },\r\n    destroy(id) {\r\n      if (!confirm(\"Czy na pewno chcesz usunąć to zdjęcie z galerii?\")) return;\r\n      this.setLoading(true);\r\n      axios\r\n        .delete(\"/api/gallery/delete/\" + id)\r\n        .then((res) => {\r\n          this.setLoading(false);\r\n          this.$store.commit(\"setSnackbar\", SnackbarAlerts.success);\r\n          this.loadGallery();\r\n        })\r\n        .catch((err) => {\r\n          this.$store.commit(\"setSnackbar\", SnackbarAlerts.error);\r\n          this.setLoading(false);\r\n          console.log(err);\r\n        });\r\n    },\r\n    send() {\r\n      if (this.getFormData().length > 0) this.setLoading(true);\r\n      for (let photo of this.getFormData())\r\n        photo.id ? this.edit(photo) : this.add(photo);\r\n    },\r\n    updateDeletedPhoto(path) {\r\n      this.gallery.splice(\r\n        this.gallery.findIndex((photo) => photo.path == path)\r\n      );\r\n    },\r\n    loadCurrentObject() {\r\n      axios\r\n        .get(\r\n          `/api/${this.$route.path.split(\"/\")[2]}/get_one/${\r\n            this.$route.params.id\r\n          }`\r\n        )\r\n        .then((res) => {\r\n          this.currentObject = res.data;\r\n        });\r\n    },\r\n    loadGallery() {\r\n      axios\r\n        .get(\r\n          `/api/gallery/get_photos/${this.$route.path.split(\"/\")[2]}/${\r\n            this.$route.params.id\r\n          }`\r\n        )\r\n        .then((res) => {\r\n          this.apiGallery = res.data;\r\n          this.gallery = res.data;\r\n          this.setLoading(false);\r\n        });\r\n    },\r\n  },\r\n\r\n  created() {\r\n    if (this.$route.path.split(\"/\")[2] != \"realizations\")\r\n      this.loadCurrentObject();\r\n    this.loadGallery();\r\n    this.setLoading(true);\r\n  },\r\n};\r\n</script>\r\n\r\n<style>\r\n.gallery-form-photo {\r\n  background-size: cover;\r\n  width: 100%;\r\n  height: auto;\r\n  margin: auto;\r\n}\r\n\r\n.close-icon {\r\n  /*filter: invert(1);*/\r\n}\r\n.close-icon-container {\r\n  position: absolute;\r\n  left: 0;\r\n  top: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  display: flex;\r\n  justify-content: flex-end;\r\n  align-items: flex-start;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
